'use babel';

import {
  createGroupButtons,
  createButton,
  createIcon,
  createIconFromPath,
  createText,
  createElement,
  insertElement
} from '../element/index';

export class ConsoleView {
  private element: HTMLElement;
  constructor () {
    this.element = createElement('atom-bugs-console');
    this.element.setAttribute('tabindex', '-1');
  }
  
  getElement () {
    return this.element;
  }
}
